<template>
  <v-speed-dial
    v-model="settingsMenu"
    :top="true"
    :right="true"
    direction="bottom"
    transition="slide-y-transition"
  >
    <template v-slot:activator>
      <v-btn
        v-model="settingsMenu"
        :color="
          settings.difficulty
            ? settings.difficulty === 2
              ? 'deep-purple'
              : 'purple'
            : 'blue'
        "
        dark
        fab
      >
        <v-icon v-if="settingsMenu"> mdi-close </v-icon>
        <v-icon v-else>
          {{
            settings.difficulty
              ? settings.difficulty === 2
                ? "mdi-skull"
                : "mdi-chess-king"
              : "mdi-baby-bottle"
          }}
        </v-icon>
      </v-btn>
    </template>
    <v-btn fab dark small color="blue" @click="setDifficulty(0)">
      <v-icon>mdi-baby-bottle</v-icon>
    </v-btn>
    <v-btn fab dark small color="purple" @click="setDifficulty(1)">
      <v-icon>mdi-chess-king</v-icon>
    </v-btn>
    <v-btn fab dark small color="deep-purple" @click="setDifficulty(2)">
      <v-icon>mdi-skull</v-icon>
    </v-btn>
  </v-speed-dial>
</template>

<script>
import { mapActions, mapState } from 'vuex'

export default {
  name: 'Game',
  computed: mapState({
    settings: (state) => state.settings.settings
  }),
  data: () => ({
    settingsMenu: false
  }),
  methods: {
    setDifficulty (val) {
      this.changeDifficulty(val)
    },
    ...mapActions('settings', ['changeDifficulty'])
  }
}
</script>

<style lang="scss">
body {
  .v-speed-dial {
    position: absolute;
  }
}
</style>
